import React, { useState, useRef, useEffect } from "react";
import { Billboard } from "@react-three/drei";
import { useFrame } from "@react-three/fiber";
import * as THREE from "three";
import { setTargetImage } from "../actions.js";

const aspectRatio = 16 / 16;
const thumbHeight = 16;
const thumbWidth = thumbHeight * aspectRatio;

export const PhotoNode = ({
  id,
  position,
  targetImage,
}) => {
  const textureUrl = `${import.meta.env.BASE_URL}assets/images/${id}`;
  console.log(`[PhotoNode.jsx] Attempting to load texture for ID ${id} from URL: ${textureUrl}`);
  
  const meshRef = useRef();
  const groupRef = useRef();
  const [texture, setTexture] = useState(null);
  const targetPosition = useRef(new THREE.Vector3(...position));
  const currentPosition = useRef(new THREE.Vector3(...position));
  
  // Load texture with error handling
  useEffect(() => {
    const loader = new THREE.TextureLoader();
    loader.load(
      textureUrl,
      (loadedTexture) => {
        setTexture(loadedTexture);
        console.log(`[PhotoNode.jsx] Texture loaded successfully for ${id}`);
      },
      undefined,
      (error) => {
        console.error(`[PhotoNode.jsx] Failed to load texture for ${id}:`, error);
        setTexture(null);
      }
    );
  }, [textureUrl, id]);
  
  // Update target position when position prop changes
  useEffect(() => {
    targetPosition.current.set(...position);
  }, [position]);
  
  const isActive = targetImage === id;
  const nodeOpacity = !targetImage || isActive ? 1 : 0.2;
  
  // Animate position
  useFrame((state, delta) => {
    if (groupRef.current) {
      currentPosition.current.lerp(targetPosition.current, delta * 2);
      groupRef.current.position.copy(currentPosition.current);
    }
    
    if (meshRef.current && meshRef.current.material) {
      meshRef.current.material.opacity = THREE.MathUtils.lerp(
        meshRef.current.material.opacity,
        nodeOpacity,
        delta * 5
      );
    }
  });
  
  console.log(`[PhotoNode.jsx] ID: ${id}, targetImage: ${targetImage}, isActive: ${isActive}, nodeOpacity: ${nodeOpacity}`);

  return (
    <group
      ref={groupRef}
      onClick={(e) => {
        e.stopPropagation();
        setTargetImage(id);
        console.log(`[PhotoNode.jsx] Clicked. Setting targetImage to: ${id}`);
      }}
    >
      <Billboard>
        <mesh ref={meshRef} scale={[thumbWidth, thumbHeight, 1]}>
          <planeGeometry />
          <meshStandardMaterial
            map={texture}
            color={texture ? "white" : "red"}
            transparent={true}
            opacity={nodeOpacity}
            side={THREE.DoubleSide}
          />
        </mesh>
      </Billboard>
    </group>
  );
}
